import { Component, OnInit } from '@angular/core';
import { Securityservice } from './services/securityservice';
import { Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{
  
  constructor(private securityservice:Securityservice, private router : Router){
    this.securityservice.currentUsername.subscribe(user=>
      this.username = user)

  }
  ngOnInit(): void {
    console.log('load app Module')
    let code = window.location.href.indexOf('code')
    this.username = localStorage.getItem("username");
    console.log("in appModule "+this.username)
  }
  title = 'app';
  username:String;
  
i:number =0;


changeActive(i){
    this.i = i;
  }

  login(){
    //alert('login request')
    this.securityservice.getTokenCode();
  }
  logout(){
    //alert('logout');
    this.securityservice.removeLoggedUser();
    this.router.navigate(['/'])
  }
}
