import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { UniversityComponent } from './university/university.component';
import { StudentComponent } from './student/student.component';
import {Routes, RouterModule} from '@angular/router';
import { FormsModule }   from '@angular/forms';
import { EnseignmentComponent } from './enseignment/enseignment.component';
import { EnseignmentChildComponent } from './enseignment-child/enseignment-child.component';
import { LoginComponent } from './login/login.component';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { AuthorizationInterceptor } from './services/authorization-interceptor';
import { AuthGuardService } from './services/auth-guard.service';
import { ErrorloginInterceptorService } from './services/errorlogin-interceptor.service';

const routes : Routes =[
  {path:'student',component:StudentComponent,canActivate: [AuthGuardService], 
  data: { roles: ['ROLE_ROLE']}},
  /*{
    path: '',
    redirectTo: "/login",
    pathMatch: 'full'
  },*/
  {path:'', component:LoginComponent },
  
  {path:'app', component : AppComponent},
  {path:'depart', component:UniversityComponent},
  { path : 'enseign', component:EnseignmentComponent,
children:[
  {path:'enseignmentChild',component:EnseignmentChildComponent}
]},
{path: '**', component: LoginComponent}

]
@NgModule({
  declarations: [
    AppComponent,
    UniversityComponent,
    StudentComponent,
    EnseignmentComponent,
    EnseignmentChildComponent,
    LoginComponent
  ],
  imports: [
    BrowserModule,FormsModule,RouterModule.forRoot(routes), HttpClientModule
  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: AuthorizationInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: ErrorloginInterceptorService, multi: true}
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
