import { Component, OnInit } from '@angular/core';
import { Securityservice } from '../services/securityservice';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor(private securityservice:Securityservice, private router : Router) { }
  username:string;

  ngOnInit(){
    this.username = localStorage.getItem('username');
    console.log('in loginComponent '+this.username)
    if(this.securityservice.isLoggedIn()) return;
    let codeExist = window.location.href.indexOf('code');
    if(codeExist == -1) return;
    let code  = window.location.href.substring(codeExist+5);
    
    console.log('code is '+code);
    /**
     * send token request
     */
    let token  = this.securityservice.getToken(code);
    if(localStorage.getItem('username')) this.router.navigate(['/app'])
    

  }

  login(){
    this.securityservice.getTokenCode();

  }

  

}
